export const macros = {
  "\\E": "\\mathbb{E}\\left[ #1 \\right]",
  "\\P": "\\mathbb{P}\\left(#1\\right)",
  "\\R": "\\mathbb{R}",
  "\\anote": "\\triangleright \\text{ #1}",
  "\\<": "\\langle",
  "\\>": "\\rangle",
  "\\(": "\\left(",
  "\\)": "\\right)",
};



